@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Raleway:wght@300;500&display=swap");

body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  background-color: antiquewhite;
  height: auto;
  /* height: 1420vh; */
}

h1 {
  font-family: "Montserrat", sans-serif;
}

p {
  font-family: "Raleway", sans-serif;
  font-weight: 300;
}

li {
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  list-style: none;
}

nav {
  display: flex;
  justify-content: center;
  gap: 10px;
  background-color: lightblue;
}
footer {
  display: flex;
  justify-content: center;
  gap: 10px;
}

@media only screen and (max-width: 600px) {
  body {
    text-align: center;
    background-color: antiquewhite;
    margin: 0;
    padding: 0;
  }
  img{
    height: 300px;
    width: 500px;
  }
}
